import React, { Component } from 'react';
import { APIUrls } from '../helpers/urls';

class dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = { jobList: [], showDashboard: false };
  }

  componentDidMount() {
    const url = APIUrls.getJobList();
    fetch(url, {
      method: 'GET',
    })
      .then((response) => response.json())
      .then((data) => {
        this.setState({ jobList: data.data.jobs });
      });
  }

  handleShowDashboard = () => {
    this.setState({ showDashboard: !this.state.showDashboard });
  };

  render() {
    const { jobList, showDashboard } = this.state;
    return (
      <div
        className={
          showDashboard ? 'dashboard-container active' : 'dashboard-container'
        }
      >
        <button onClick={this.handleShowDashboard}>
          {showDashboard ? 'Cancle' : 'Show Dashboard'}
        </button>
        {showDashboard && (
          <span>
            <div className="image-container">
              <img
                src="data:image/jpeg;base64,"
                alt="user-profile"
              />
            </div>
            <div className="detail">
              <div>
                <div className="title">Name</div>
                <div className="value">John</div>
              </div>
              <div>
                <div className="title">Email</div>
                <div className="value">John@somewhere.com</div>
              </div>
              <div>
                <div className="title">Phone</div>
                <div className="value">9999000000</div>
              </div>
              <div>
                <div className="title">Name</div>
                <div className="value">John</div>
              </div>
            </div>
            <div className="job-list">
              <h3>Job List</h3>
              <ul>
                {jobList.map((job) => {
                  return <li>{job.role + ' - ' + job.company}</li>;
                })}
              </ul>
            </div>
          </span>
        )}
      </div>
    );
  }
}

export default dashboard;
